.article {
  margin: 0 auto;
  border: 1px solid #ccc;
  width: 564px;
  min-height: 68px;
  padding: 10px;
  display: flex;
  font-family: sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.main-image {
  width: 108px;
  height: 108px;
  margin-right: 10px;
}

.heading {
  font-weight: 900;
  margin-bottom: 0px;
  margin-top: 0.9rem;
}
.description {
  font-size: 13px;
  font-weight: 100;
  margin-top: 0px;
}
.author {
}

.name {
  font-weight: bold;
  margin-bottom: 0.5em;
  margin-right: 0.3em;
  font-size: 12px;
}

.handle {
  font-weight: bold;
  font-size: 12px;
}

.logo {
  margin-right: 5px;
  width: 17px;
  height: auto;
  border-radius: 1px;
}
.buttons {
  float: right;
  margin-right: 42px;
}
.icon {
  margin-left: 5px;
  cursor: pointer;
  width: 10px;
}
